



















.navbar nav{
    padding: 0 20px;
}

.index {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 30px;
  margin: 30px;
}
.index h2{
  font-size: 1.8em;
  text-align: center;
  margin-top: 0px;
}
.index .ingredients{
  margin: 30px auto;
}
.index .ingredients li{
  display: inline-block;
  background-color: #aaa;
  border-radius: 10px;
  padding: 5px;
  margin: 5px;
}
.index .delete{
  position: absolute;
  top: 4px;
  right: 4px;
  cursor: pointer;
  color: #aaa;
  font-size: 1.4em;
}



.add-snack{
    margin-top: 60px;
    padding: 20px;
    max-width: 500px;
}
.add-snack h2{
    font-size: 2em;
    margin: 20px auto;
}
.add-snack .field{
    margin: 20px auto;
    position: relative;
}
.add-snack .delete{
    position: absolute;
    right: 0;
    bottom: 16px;
    color: #aaa;
    font-size: 1.4em;
    cursor: pointer;
}

.edit-snack{
     margin-top: 60px;
     padding: 20px;
     max-width: 500px;
}
.edit-snack h2{
     font-size: 2em;
     margin: 20px auto;
}
.edit-snack .field{
